body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
input {
  outline: none
}
a {
  text-decoration: none
}
h4 {
  margin: 0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  position: relative;
  overflow: hidden;
}
* {
  -webkit-transition: .3s ease;
  transition: .3s ease; }

@media screen and (max-width: 768px) {
  #coreBlock .input div {
    width: 45% !important; }
  #root {
    height: auto !important; }
  #gameBlock {
    height: auto !important; }
    #gameBlock .gameBlockMini {
      height: 60vh !important;
      margin-bottom: 50px;
      margin-top: 30px; } }

#root {
  width: 100%;
  height: 100vh;
  background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI1IiBoZWlnaHQ9IjUiPgo8cmVjdCB3aWR0aD0iNSIgaGVpZ2h0PSI1IiBmaWxsPSIjZmZmIj48L3JlY3Q+CjxyZWN0IHdpZHRoPSIxIiBoZWlnaHQ9IjEiIGZpbGw9IiNjY2MiPjwvcmVjdD4KPC9zdmc+"); }

#gameBlock {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap; }
  #gameBlock .gameBlockMini {
    width: 25%;
    height: 60%;
    min-width: 340px; }
    #gameBlock .gameBlockMini .name {
      display: block;
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
      height: -webkit-max-content;
      height: -moz-max-content;
      height: max-content;
      font-size: 3vh;
      margin-bottom: 25px;
      padding: 3px; }
    #gameBlock .gameBlockMini .myFlit, #gameBlock .gameBlockMini .shootingStar {
      position: relative;
      width: 100%;
      height: 90%;
      background-color: #fff;
      box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.4);
      border-radius: 5px;
      cursor: pointer;
      overflow: hidden; }
      #gameBlock .gameBlockMini .myFlit .guard, #gameBlock .gameBlockMini .shootingStar .guard {
        position: absolute;
        width: 100%;
        height: 100%; }
      #gameBlock .gameBlockMini .myFlit:hover, #gameBlock .gameBlockMini .shootingStar:hover {
        box-shadow: 0px 5px 10px 0px rgba(0, 0, 100, 0.4);
        -webkit-transform: matrix(1.01, 0, 0, 1.01, 0, 0);
                transform: matrix(1.01, 0, 0, 1.01, 0, 0); }

#gameInterface {
  width: 100%;
  height: 10vh;
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
  align-items: flex-start;
  justify-content: flex-end;
  z-index: 10000; }
  #gameInterface a {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 5vw;
    height: 10vh;
    margin: 10px;
    background-color: #fff;
    background-image: url(/static/media/cross.d704a706.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 70% 70%;
    color: rgba(0, 0, 0, 0.6);
    font-size: 16pt;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.4);
    border-radius: 50px; }
    #gameInterface a:hover {
      -webkit-transform: matrix(1.06, 0, 0, 1.06, 0, 0);
              transform: matrix(1.06, 0, 0, 1.06, 0, 0);
      box-shadow: 0px 5px 10px 0px rgba(0, 0, 100, 0.4); }

.myFlit, .shootingStar {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }
  .myFlit div:hover, .shootingStar div:hover {
    background-color: rgba(0, 0, 30, 0.15); }
  .myFlit div:active, .shootingStar div:active {
    background-color: red; }

.void, .shipblock, .explosion, .miss {
  width: 10%;
  height: 10%; }

.shipblock {
  background-color: rgba(0, 0, 100, 0.4); }

.shootingStar .shipblock {
  background-color: transparent; }

.explosion {
  background-image: url(/static/media/exp.e8b01be4.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 95% 95%;
  background-color: rgba(0, 0, 0, 0.9); }

.miss {
  background-color: red; }

.invise {
  z-index: -1000; }

.vise {
  z-index: 1000; }

.youtourn {
  font-size: 5vh;
  color: #fff;
  background: rgba(0, 0, 100, 0.4); }

#finish {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: whitesmoke;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 6vh; }

#coreBlock {
  display: flex;
  justify-content: space-around;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh; }
  #coreBlock .span {
    font-size: 4vh;
    cursor: pointer; }
  #coreBlock h4 {
    font-size: 6vh;
    text-align: center; }
  #coreBlock .input {
    margin: 100px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 12vh; }
    #coreBlock .input div {
      height: 100%;
      width: 20%;
      display: flex;
      justify-content: space-around;
      align-items: center;
      align-items: flex-start;
      flex-direction: column;
      position: relative; }
      #coreBlock .input div span {
        font-size: 3vh; }
      #coreBlock .input div input {
        border: 4px solid #0009;
        width: 100%;
        height: 50%;
        font-size: 3vh; }

